<template>
  <div class="admin">
    <div class="row">
      <div class="col">
        <h3>Tao bai viet</h3>
        <div class="addpost mt-3 mb-1">
          <div class="mb-3">
            <label for="exampleFormControlInput1" class="form-label"
              >Tieu de</label
            >
            <input
              type="text"
              class="form-control"
              id="exampleFormControlInput1"
              v-model.lazy="post.tieude"
              maxlength="150"
            />
          </div>
          <div class="mb-3">
            <label for="exampleFormControlTextarea1" class="form-label"
              >Noi dung</label
            >
            <textarea
              class="form-control"
              id="exampleFormControlTextarea1"
              rows="3"
              v-model="post.noidung"
              style="height:500px"
            ></textarea>
          </div>
        </div>
      </div>
      <div class="col">
        <h3>Xem truoc</h3>
        <div class="box" id="idChenContent">
          <h3><div v-html="post.tieude"></div></h3>
          <hr />
          <div v-html="post.mNoidung"></div>
        </div>
      </div>
    </div>
    <div class="row gap-2 mx-auto">
      <button class="col btn btn-primary" type="button" @click="xemtruoc">
        Xem truoc
      </button>
      <!-- Button trigger modal -->
      <button
        class="col btn btn-primary"
        type="button"
        @click="addPost"
        data-bs-toggle="modal"
        data-bs-target="#applyPost"
      >
        Dang bai viet
      </button>
    </div>

    <!-- Modal -->
    <div
      class="modal fade"
      id="applyPost"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            ...
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Close
            </button>
            <button type="button" class="btn btn-primary">Save changes</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { collection, addDoc } from "firebase/firestore";

export default {
  components: {},
  data: () => {
    return {
      post: {
        tieude: "",
        noidung: "",
        mTieude: "",
        mNoidung: "",
        slug: "Slug",
        bannerUrl: null,
      },
    };
  },
  methods: {
    xemtruoc: function(post, event) {
      var markdown = require("markdown").markdown;
      this.post.mTieude = markdown.toHTML("###" + this.post.tieude);
      this.post.mNoidung = markdown.toHTML(this.post.noidung);
      var str = this.post.tieude.toLowerCase();
      //Đổi ký tự có dấu thành không dấu
      str = str
        .replace(/á|à|ả|ạ|ã|ă|ắ|ằ|ẳ|ẵ|ặ|â|ấ|ầ|ẩ|ẫ|ậ/gi, "a")
        .replace(/é|è|ẻ|ẽ|ẹ|ê|ế|ề|ể|ễ|ệ/gi, "e")
        .replace(/i|í|ì|ỉ|ĩ|ị/gi, "i")
        .replace(/ó|ò|ỏ|õ|ọ|ô|ố|ồ|ổ|ỗ|ộ|ơ|ớ|ờ|ở|ỡ|ợ/gi, "o")
        .replace(/ú|ù|ủ|ũ|ụ|ư|ứ|ừ|ử|ữ|ự/gi, "u")
        .replace(/ý|ỳ|ỷ|ỹ|ỵ/gi, "y")
        .replace(/đ/gi, "d")
        //Xóa các ký tự đặt biệt
        .replace(
          /\`|\~|\!|\@|\#|\||\$|\%|\^|\&|\*|\(|\)|\+|\=|\,|\.|\/|\?|\>|\<|\'|\"|\:|\;|_/gi,
          ""
        )
        //Đổi khoảng trắng thành ký tự gạch ngang
        .replace(/ /gi, "-")
        //Đổi nhiều ký tự gạch ngang liên tiếp thành 1 ký tự gạch ngang
        //Phòng trường hợp người nhập vào quá nhiều ký tự trắng
        .replace(/\-\-\-\-\-/gi, "-")
        .replace(/\-\-\-\-/gi, "-")
        .replace(/\-\-\-/gi, "-")
        .replace(/\-\-/gi, "-")
        .replace(/\@\-|\-\@|\@/gi, "");
      //In slug ra textbox có id “slug”
      this.post.slug = str;
    },
    addPost: function() {
      (async () => {
        console.log(this.post.tieude);
        try {
          const docRef = await addDoc(collection(db, "posts"), {
            title: this.post.tieude,
            content: this.post.mNoidung,
            slug: this.post.slug,
          });
          console.log("Document written OK");
        } catch (e) {
          console.error("Error adding document: ", e);
        }
      })();
    },
  },
};
</script>

